<template>
  <div class="reg">
    <el-col :span="6" :xs="22">
      <el-card shadow="always">
        <div class="card_header">
          <h2>后台管理系统</h2>
        </div>
        <el-form
          :model="user"
          :rules="rules"
          ref="user"
          class="demo-user"
          label-position="left"
        >
          <el-form-item prop="MemberName">
            <el-input v-model="user.MemberName" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <el-form-item prop="MemberMobile">
            <el-input v-model="user.MemberMobile" placeholder="请输入手机号"></el-input>
          </el-form-item>
          <el-form-item prop="MemberPassword">
            <el-input v-model="user.MemberPassword" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item prop="MemberPassword1">
            <el-input v-model="user.MemberPassword1" placeholder="请输入确认密码"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="btn-reg" @click="reg('user')">立即注册</el-button>
          </el-form-item>
        </el-form>
        <span><router-link to="/login" style="color:#606266">去登录</router-link></span>
      </el-card>
    </el-col>
  </div>
</template>
<script>
import '../assets/login_bg.jpg'
export default {
  data() {
    // 注意：必須在data函數裡定義此箭頭函數，才能確保this.form使用
    const samePwdFn = (rule, value, callback) => {
      if (value !== this.user.MemberPassword) {
        // 如果驗證失敗，則調用 回調函數 時，制定一個 Error 對象
        callback(new Error('兩次輸入的密碼不一致！'))
      } else {
        // 如果驗證成功，則直接調用callback回調函數
        callback()
      }
    }
    return {
      user: {
        MemberName: "",
        MemberMobile: "",
        MemberPassword:'',
        MemberPassword1:''
      },
      rules: {
        MemberName: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, message: "长度不能少于3个字符", trigger: "blur" },
        ],
        MemberMobile:[
          { required: true, message: "请输入手机号", trigger: "blur" },
          { pattern: /^1[3-9][0-9]{9}$/, message: "请输入正确的手机号", trigger: "blur" },
        ],
        MemberPassword: [
          { required: true, message: '請輸入密碼', trigger: 'blur' },
          {
            pattern: /^\S{6,15}$/,
            message: '密碼必須是6-15的非空字符',
            trigger: 'blur'
          }
        ],
        MemberPassword1: [
          { required: true, message: '請再次輸入密碼', trigger: 'blur' },
          {
            pattern: /^\S{6,15}$/,
            message: '密碼必須是6-15的非空字符',
            trigger: 'blur'
          },
          { validator: samePwdFn, trigger: 'blur' }
        ]
      },
    };
  },
  methods: {
    reg() {
      this.$refs.user.validate((valid) => {
        if (valid) {
          alert(`验证通过`)
        this.$axios.post("/index.php/Api/Reg", {
          params: this.user,
        })
        .then((res) => {
          // this.data = res.data.Data;
          console.log(res)
        })
        .catch((error) => {
          console.log(error);
        });
        } else {
          console.log("error");
          return false;
        }
      })
      // this.$axios.post({"/index.php/Api/Reg",
      //   params:this.user
      // })
    },
  },
};
</script>
<style lang="scss" scoped>
  .reg {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../assets/login_bg.jpg');
    background-size: cover;
  }
  .btn-reg {
    width: 100%;
  }
  .card_header{
    display: flex;
    justify-content: center;
    align-items: center;
    color: #36d;
    &>h2{
      margin-top: 0;
    }
  }
</style>